	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Trapezium</h2>\n<p>Area of a Trapezium is defined by the 2D space enclosed by the Trapezium. A trapezium is a Quadrilateral by 2 oposite parallel sides of equal or unequal length and 2 opposite non parallel sides of equal or unequal length.</p>\n<p>Let the parallel sides be represented by ‘a’ and ‘b’ and the perpendicular distance between these parallel sides be ‘h’\nthen\nThe Area of the Trapezium = 1/2 * (a+b) * h</p>\n<p> which means Area of Trapezium = 1/2 * (sum of parallel sides) * (perpendicular distance between parallel sides).</p>\n<h2>Note:</h2>\n<p> Area of trapezium can also be found out by dividing the trapezium into a series of triangle and rectangle and finally adding their areas separately.</p>","fields":{"slug":"/mathematics/area-of-a-trapezium/"},"frontmatter":{"title":"Area of a Trapezium"}}},"pathContext":{"slug":"/mathematics/area-of-a-trapezium/"}}

